<template>
  <section class="checkFeeds pt-2 mx-3">
    <page-intro :introName="introName"></page-intro>
    <v-card>
      <v-card-title>
        <v-layout align-start
                  justify-space-between
                  row
                  fill-height>
          <v-flex xs4>
            <v-text-field clearable
                          label="Lookup Feeds..."
                          v-model="lookup"
                          single-line
                          hide-details></v-text-field>
          </v-flex>
          <v-flex xs1>
            <v-btn :loading="loading"
                   color="tfgprimary"
                   class="mt-3 ml-3"
                   round
                   small
                   dark
                   @click="SearchFeeds()">
              Find
            </v-btn>

          </v-flex>
          <v-flex xs3>
            <div v-if="selected.length > 0"
                 class="pl-5">
              <v-dialog v-model="StartDialog"
                        max-width="400">
                <v-btn slot="activator"
                       fab
                       dark
                       small
                       color="secondary">
                  <v-icon dark>play_arrow</v-icon>
                </v-btn>
                <v-card>
                  <v-card-actions>
                    <v-text-field v-model="changeNumber"
                                  :error-messages="changeNumberErrors"
                                  label="Change Number"
                                  hint="Example C123456"
                                  required
                                  clearable
                                  autofocus
                                  class="pr-5"
                                  @input="$v.changeNumber.$touch()"
                                  @blur="$v.changeNumber.$touch()">
                    </v-text-field>
                    <v-btn color="tfgprimary"
                           round
                           :dark="!this.$v.$invalid"
                           small
                           :disabled="this.$v.$invalid"
                           @click="StartFeeds()">Start</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
              <v-dialog v-model="StopDialog"
                        max-width="400">
                <v-btn slot="activator"
                       fab
                       dark
                       small
                       color="error">
                  <v-icon dark>stop</v-icon>
                </v-btn>
                <v-card>
                  <v-card-actions>
                    <v-text-field v-model="changeNumber"
                                  :error-messages="changeNumberErrors"
                                  label="Change Number"
                                  hint="Example C123456"
                                  required
                                  clearable
                                  autofocus
                                  class="pr-5"
                                  @input="$v.changeNumber.$touch()"
                                  @blur="$v.changeNumber.$touch()">
                    </v-text-field>
                    <v-btn color="error"
                           round
                           :dark="!this.$v.$invalid"
                           small
                           :disabled="this.$v.$invalid"
                           @click="StopFeeds()">Stop</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </div>
          </v-flex>
          <v-flex xs4>
            <v-text-field v-model="search"
                          prepend-icon="filter_list"
                          label="Filter results"
                          single-line
                          hide-details></v-text-field>
          </v-flex>
        </v-layout>
      </v-card-title>
      <bizTable :data="feeds"
                :search="search"
                @selectall="selected = $event">
      </bizTable>
    </v-card>
  </section>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required, minLength } from "vuelidate/lib/validators";
import { GetFeeds2013Test, StopFeed, StartFeed } from "@/services/api/Biztalk";
import { bus } from "@/main";
import { Notify } from "@/services/HelperService";
import bizTable from "@/components/Biztalk/table";
import PageIntro from "@/components/PageIntro.vue";

export default {
  mixins: [validationMixin],

  validations: {
    changeNumber: { required, minLength: minLength(6) }
  },

  components: {
    bizTable,
    PageIntro
  },
  data() {
    return {
      introName: "Biztalk 2013 Feeds - UAT",
      loading: false,
      feeds: [],
      StopDialog: false,
      StartDialog: false,
      changeNumber: "",
      lookup: "",
      search: "",
      selected: []
    };
  },
  methods: {
    SearchFeeds() {
      GetFeeds2013Test(this.lookup)
        .then(data => {
          this.feeds = data;
        })
        .catch(error => {
          this.$notify(Notify(error));
        })
        .finally(() => {
          this.loading = false;
        });
    },
    StopFeeds() {
      this.loading = true;
      StopFeed({
        Feeds: this.selected,
        ChangeNumber: this.changeNumber,
        Lookup: this.lookup
      })
        .catch(error => {
          this.$notify(Notify(error));
        })
        .finally(() => {
          this.SearchFeeds();
          bus.$emit("ClearBizSelected");
          this.selected = [];
          this.StopDialog = false;
          this.changeNumber = "";
          this.loading = false;
        });
    },
    StartFeeds() {
      StartFeed({
        Feeds: this.selected,
        ChangeNumber: this.changeNumber,
        Lookup: this.lookup
      })
        .catch(error => {
          this.$notify(Notify(error));
        })
        .finally(() => {
          this.SearchFeeds();
          bus.$emit("ClearBizSelected");
          this.selected = [];
          this.StopDialog = false;
          this.changeNumber = "";
          this.loading = false;
        });
    }
  },
  computed: {
    changeNumberErrors() {
      const errors = [];
      if (!this.$v.changeNumber.$dirty) return errors;
      !this.$v.changeNumber.required &&
        errors.push("Change Number is required.");
      return errors;
    }
  }
};
</script> 

<style lang="scss" scoped>
p {
  margin-bottom: 0px;
}
</style>
