export const GetFeeds2013Test = search => {
  return HTTP.get("/BizFeeds/Check2013TestFeeds/", {
    params: {
      searchString: search
    }
  }).then(Response => {
    return Response.data;
  });
};

export const StopFeed = payload => {
  return HTTP.post("/BizFeeds/StopFeed", {
    Feeds: Object.keys(payload.Feeds)
      .map(i => payload.Feeds[i].location)
      .toString(),
    ChangeNumber: payload.ChangeNumber
  }).then(Response => {
    return Response.data;
  });
};

export const StartFeed = payload => {
  return HTTP.post("/BizFeeds/StartFeed", {
    Feeds: Object.keys(payload.Feeds)
      .map(i => payload.Feeds[i].location)
      .toString(),
    ChangeNumber: payload.ChangeNumber
  }).then(Response => {
    return Response.data;
  });
};
